import { ChangeEvent, useEffect, useState } from 'react';

import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Container, NativeSelect } from '@mui/material';

import { FukusyoData } from './FukusyoData';

export const FukusyoTable = () => {
  const [list, setList] = useState<FukusyoData[]>(null);
  const [firstKey, setFirstKey] = useState('off');
  const [secondKey, setSecondKey] = useState('off');
  const [rarity, setRarity] = useState(0);

  const handleChangeFirst = (event: ChangeEvent<HTMLSelectElement>) => {
    setFirstKey(event.target.value);
    setSecondKey('off');
  };
  const handleChangeSecond = (event: ChangeEvent<HTMLSelectElement>) => {
    setSecondKey(event.target.value);
  };
  const handleRarity = (event: ChangeEvent<HTMLSelectElement>) => {
    setRarity(event.target.value ? Number(event.target.value) : 0);
  };

  const fileName = './fukusyo.csv?v=20250426';

  const load = async () => {
    const response = await fetch(fileName, { method: 'GET' });
    const text = await response.text();
    const data = text
      .trim()
      .split('\n')
      .map((line, index) => {
        const array = line.split(/[\s\t]+/).map((x) => x.trim());
        const v = {
          index: index,
          name: array[0],
          kou: Number(array[1]),
          bou: Number(array[2]),
          kei: Number(array[3]),
          hei: Number(array[4]),
          total: Number(array[5]),
          kind: Number(array[6]),
        } as FukusyoData;
        return v;
      });
    data.sort((a, b) => b.total - a.total);
    setList(data);
    setRarity(0);
  };
  const filter = (targetList: FukusyoData[]) => {
    if (!targetList) {
      return [];
    }
    const filteredList = targetList.filter((row) => rarity === 0 || row.kind === rarity);
    return filteredList
  }
  const sort = () => {
    if (!list || list.length === 0) {
      return [];
    }
    if(!firstKey || firstKey === 'off'){
      const sortedList = [...list].sort((a, b) => b.total - a.total);
      setList(sortedList);
    }else{
      const sortedList = [...list].sort((a, b) => {
        let r = b[firstKey] - a[firstKey];
        if (r === 0 && secondKey && secondKey !== 'off') {
          r = b[secondKey] - a[secondKey];
        }
        return r;
      });
      setList(sortedList);
    }
  };

  useEffect(() => {
    (async () => {
        await load();
    })();
  }, []);
  useEffect(() => {
    if(list){
      setList([...list]);
    }
  }, [rarity])
  useEffect(() => {
    sort();
  }, [firstKey, secondKey]);
  const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
      fontSize: '28px',
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: '28px',
    },
  }));

  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
      border: 0,
    },
  }));
  return list ? (
    <Container maxWidth="md" disableGutters>
      <Box sx={{ minWidth: 290, fontSize: '26px' }}>
        <FormControl sx={{ m: 1, minWidth: 140 }}>
          <InputLabel variant="standard" htmlFor="uncontrolled-native">
            
          </InputLabel>
          <NativeSelect
            value={firstKey}
            onChange={handleChangeFirst}
            style={{ fontSize: '26px', marginLeft: '10px'}}
            variant='standard'
            inputProps={{
              name: 'firstKey',
              id: 'uncontrolled-native',
            }}
          >
            <option value={'off'}></option>
            <option value={'kou'}></option>
            <option value={'bou'}></option>
            <option value={'kei'}></option>
            <option value={'hei'}></option>
          </NativeSelect>
        </FormControl>
        <FormControl
          sx={{ m: 1, minWidth: 140 }}
          disabled={!(firstKey && firstKey !== 'off')}
        >
          <InputLabel variant="standard" htmlFor="uncontrolled-native2">
            
          </InputLabel>
          <NativeSelect
            value={secondKey}
            onChange={handleChangeSecond}
            style={{ fontSize: '26px', marginLeft: '10px'}}
            variant='standard'
            inputProps={{
              name: 'secondKey',
              id: 'uncontrolled-native2',
            }}
          >
            <option value={'off'}></option>
            {firstKey === 'kou' ? undefined : (
              <option value={'kou'}></option>
            )}
            {firstKey === 'bou' ? undefined : (
              <option value={'bou'}></option>
            )}
            {firstKey === 'kei' ? undefined : (
              <option value={'kei'}></option>
            )}
            {firstKey === 'hei' ? undefined : (
              <option value={'hei'}></option>
            )}
          </NativeSelect>
        </FormControl>

        <FormControl sx={{ m: 1, minWidth: 140 }}>
          <InputLabel variant="standard" htmlFor="uncontrolled-native3">
            
          </InputLabel>
          <NativeSelect
            value={rarity}
            onChange={handleRarity}
            style={{ fontSize: '26px', marginLeft: '10px'}}
            variant='standard'
            inputProps={{
              name: 'rarity',
              id: 'uncontrolled-native3',
            }}
          >
            <option value={'0'}></option>
            <option value={'1'}></option>
            <option value={'2'}></option>
          </NativeSelect>
        </FormControl>
      </Box>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 350 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <StyledTableCell align='center'>#</StyledTableCell>
              <StyledTableCell></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right">total</StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {filter(list).map((row, i) => (
              <StyledTableRow
                key={row.index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <StyledTableCell align="center">{i+1}</StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {row.name}
                </StyledTableCell>
                <StyledTableCell align="right">{row.kou}</StyledTableCell>
                <StyledTableCell align="right">{row.bou}</StyledTableCell>
                <StyledTableCell align="right">{row.kei}</StyledTableCell>
                <StyledTableCell align="right">{row.hei}</StyledTableCell>
                <StyledTableCell align="right">{row.total}</StyledTableCell>
                <StyledTableCell align="center">
                  <p style={{color: (row.kind === 1 ? 'purple' : 'darkorange')}}>{(row.kind === 1 ? '' : '')}</p>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  ) : undefined;
};
